import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import FormGroup from "@mui/material/FormGroup";

const dispatchedEvents = [];

const SrsMuiFormGroup = function (props, ref) {
  return (
    <FormGroup {...props} theme={props.muiTheme}>
      <slot />
    </FormGroup>
  );
};

const styles = `
  /* Custom styles for the MuiFormGroup component */
`;

const props = [
  {
    name: "row",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: ["srs-mui-form-control-label"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiFormGroup),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
